@import "variables";

.contact-page {
  &__item-wrapper {
    display: flex;
  }

  &__item {
    width: max-content;
    background-color: $blue-gray-color;
    padding: 3rem;
  }

  &__item-img {
    border: 1px solid $text-color;
    line-height: 50px;
    padding: 0 14px;
    transition-duration: 1s;

    &:hover {
      color: $blue-color;
      border: 1px solid $blue-color;
    }
  }

  &__item-info {
    margin-left: 1rem;
  }

  &__item-wrapper {
    width: 250px;
  }
}